import { Col, Container, Row } from 'react-bootstrap';
import ImageGallery from 'react-image-gallery';

import './gallery-body.css';
import 'react-image-gallery/styles/css/image-gallery.css';
import { useEffect, useState } from 'react';
import { getGalleryImagesByHotelAndImageType } from '../../../api/imageAPI';

interface props {
  hotelId: number;
  hotelName: string;
}

function GalleryBody({ hotelId, hotelName }: props) {
  const [galleryImg, setGalleryImg] = useState([]);
  const [imgLoading, setImgLoading] = useState(false);

  //get image list
  useEffect(() => {
    getGalleryImagesByHotelAndImageType(hotelId, 'GALLERY').then((data) => {
      try {
        if (data['result']) {
          if (data['result'] === 'SUCCESS') {
            setGalleryImg(data['data']);
            setImgLoading(true);
          } else if (data['result'] === 'FAILED') {
          } else {
          }
        } else {
        }
      } catch (error) {}
    });
  }, [hotelId]);

  return (
    <div className="accommodations-body-div pt-5 pb-5">
      <Container>
        <Row className="justify-content-md-center text-center mb-5">
          <Col lg="8">
            <h1>{hotelName}</h1>
            <h4>Celebra tu Boda en {hotelName}</h4>
            <p>Increíble ubicación para que sus vacaciones sean inolvidables</p>
          </Col>
        </Row>

        {imgLoading && (
          <Row className="justify-content-md-center text-center mb-5">
            <Col lg="8">
              <ImageGallery items={galleryImg} />
            </Col>
          </Row>
        )}
      </Container>
    </div>
  );
}

export default GalleryBody;
